.dialogue {
    background-color: #47aad4;
    margin: 10px auto 10px auto;
    width: 280px;
    height: 80px;
    padding: 10px 0px 10px 0px;
    border: 5px solid white;
    border-radius: 25px 25px 0px 0px;
    display: block;
}

.flower {
    background-color: #47aad4;
    width: 125px;
    height: 135px;
    padding: 10px 0px 10px 0px;
    border: 5px solid white;
    border-radius: 0px 25px 25px 0px;
    position: relative;
    margin: 0px 10px 0px 0px;
}

.option {
    background-color: #47aad4;
    width: 125px;
    height: 135px;
    padding: 10px 0px 10px 0px;
    border: 5px solid white;
    border-radius: 25px 0px 0px 25px;
    position: relative;
    margin: 0px 0px 0px 10px;
}

.centerFlex {
    width: 290px;
    display: flex;
    flex-flow: row no-wrap;
    margin: 0 auto;
    left: 0px;
    top: 0px;
}

.phase {
    background-color: #47aad4;
    margin: 10px auto 10px auto;
    width: 280px;
    height: 25px;
    padding: 10px 0px 10px 0px;
    border: 5px solid white;
    border-radius: 0px 0px 25px 25px;
    display: block;
}

.fullFlower {
    width: 120px;
    height: 144px;
    position: relative;
    margin: 0 auto;
}

h1 {
    font-size: 20px;
    color: #327d9c;
    text-align: center;
    text-shadow: -2px -2px white, 2px -2px white, -2px 2px white, 2px 2px white;
    position: relative;
    margin: 0;
}

hr {
    background-color: white;
    width: 180px;
    height: 4px;
    border: none;
    position: relative;
    margin: 0 auto;
}

p {
    font-size: 14px;
    color: rgb(61, 61, 61);
    text-align: center;
    position: relative;
    margin: 0;
}

.optionFlex {
    width: 120px; 
    height: 140px;
    display: flex;
    flex-flow: column wrap;
    margin: 0 auto;
}

button {
    font-size: 9px;
    font-family: Anton-Regular;
    background-color: #327d9c;
    color: white;
    border: 4px solid rgb(61, 61, 61);
    border-radius: 25px;
    margin: auto;
    width: 90px;
    height: 30px;
    text-align: center;
    transition: 0.5s ease-out;
}

button:hover {
    font-size: 12px;
    background-color: #47aad4;
    width: 120px;
    height: 40px;
    cursor:pointer;
}